import React, { PureComponent } from 'react';
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { LoginWrapper, LoginBox, Input, Button } from "./style";
import { action } from "./store";

class Login extends PureComponent {
  render() {
    return (
      <LoginWrapper>
        <LoginBox>
          <Input placeholder='账号' ref={(input) => { this.account = input }} />
          <Input placeholder='密码' type='password' ref={(input) => { this.password = input }} />
          <Button onClick={() => this.props.login(this.account, this.password,this.props.history)}>登陆</Button>
        </LoginBox>
      </LoginWrapper>
    );
  }

}

const mapDispatch = (dispatch) => ({
  login(account, password,history) {
    dispatch(action.Login(account.value, password.value,history))
  }
})

export default connect(null, mapDispatch)(withRouter(Login));